<template>
  <div v-if="!item.hidden" class="item-wrapper">
    <template v-if="!item.children|| (item.children.length===1&&!item.children.hidden&&!item.alwaysShow)">
      <el-menu-item :index="getPath">
          <font-awesome-icon v-if="item.meta.icon" :icon="item.meta.icon" size="lg" fixed-width />
        <template slot="title">{{item.meta.title}}</template>   
      </el-menu-item>
    </template>
    <template v-else>
      <el-submenu :index="getPath">
        <template slot="title">
          <font-awesome-icon :icon="item.meta.icon" size="lg" fixed-width/>
          <span slot="title">{{ item.meta.title }}</span>
        </template>
       <template v-if="item.children&&!item.children.hidden">
         <menu-item v-for="child in item.children" :key="child.path" :item="child" :basePath="item.path"></menu-item>
       </template>
      </el-submenu>
    </template>
  </div>
</template>

<script>
export default {
  name: "menuItem",
  props: {
    item: {
      type: Object,
      required: true
    },
    basePath:{
      type:String,
      default:''

    }
  },
  computed: {
    getPath() {
      if (this.item.redirect) {
        return this.item.redirect;
      }
      if(this.item.path.includes(":")){
        return this.basePath+'/'+this.item.path.split(":")[0]
      }else{
        return this.basePath+'/'+this.item.path
      }
           
      
        
        
      }
    }
  }
</script>