import { getAllGrade } from '@/api/grade'

const state = {
    classes: []
}

const mutations = {
    SET_CLASSES(state, classes) {
        state.classes = classes
    }
}

const actions = {
    getClasses({ commit }) {
        return new Promise((resolve, reject) => {
            getAllGrade().then(resp => {
                commit('SET_CLASSES', resp.data)
                resolve(resp.data)
            }).catch(error => {
                reject(error)
                throw new Error(error)
            })
        })
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}