const state={
    isCollapse:false
}

const mutations = {
    SET_SIDEBAR(state,flag){
        state.isCollapse = flag
    }
}

const actions = {
    openSidebar({commit}){
        commit('SET_SIDEBAR',true)
    },
    closeSidebar({commit}){
        commit('SET_SIDEBAR',false)
    }
}

export default{
    namespaced:true,
    state,
    mutations,
    actions
}